}

.page {
  scroll-behavior: smooth;
  background-color: #f8f8f8;
}

.page::-webkit-scrollbar {
  width: 9px;
  height: 9px;
}

.page::-webkit-scrollbar-thumb {
  background-color: #03839e;
  border-radius: 20px;
}

.page::-webkit-scrollbar-thumb:active {
  background-color: #03719e;
}

.page::-webkit-scrollbar-track {
  background-color: #ffec7a;
  border-radius: 20px;
}

.page__body {
  min-width: 320px;
}

.page__menu {
  opacity: 0;
  pointer-events: none;
  z-index: 10;
  transition: all .3s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateX(100%);
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
  transform: translateX(0);
}

.page:has(.page__menu:target) {
  overflow: hidden;
}

